<template>
	<div>
		<Row>
      <Form ref="filterFormRef" :model="filterFormModel" :label-width="80">
				<FormItem label="产品名称" style="width:15%">
					<Input v-model="filterFormModel.productName" placeholder="Enter something..."></Input>
				</FormItem>
				<FormItem label="用途类型" style="width:15%">
					<Select v-model="filterFormModel.usage">
						<Option value="0">全部</Option>
						<Option value="1">订货方式</Option>
					</Select>
				</FormItem>
				<FormItem  style="width:15%">
					<Button  type="primary">查询</Button>
				</FormItem>
			</Form>
		</Row>
		<Row>
			<Table width="100%" :columns="tableColumn" :data="tableData"></Table>
		</Row>
		<Row style="margin-top:20px;">
			<Page :total="40" show-elevator show-sizer />
			</Row>	</div>
</template>
<script>
 export default {
	data() {
		return {
			formCustom:{},
			filterFormModel:{},
			tableData:[
        {
          "product_mame":"阿道夫",
          "usage":"加分",
          "update_time":"2019年2月22日18:11:22",
        }
      ],
			tableColumn:[
				{
					title:"产品名称",
					key:"product_mame"
				},
				{
					title:"用途类型",
					key:"usage"
				},
				{
					title:"更新日期",
					key:"update_time"
				},
				{
					title:"操作",
					key:"action",
					render: (h, params) => {
						return h('div', [
							h('Button', {
									props: {
										type: 'primary',
										size: 'small'
									},
									style: {
										margin: '5px',
									}
							}, "修改"),
							h('Button', {
									props: {
										type: 'error',
										size: 'small'
									},
									style: {
										margin: '5px',
									}
							}, "删除"),
						]);
					}
				}
			]
		}
	}
}
</script>
<style scoped>
	form>div{
		display:inline-block;
		float:left;
	}
</style>
